    //   'textLong':'<span class=\'CapUp\'>Jag har ftt frgan ett antal gnger<span> <span class="CapDown">om jag kan gra en logotype.\u00a0<BR><BR>Lite ltt och snabbt...<BR><BR>Det r inte s ltt som man tror. Man behver gra ett ordentligt grundjobb och faktiskt vrka ut logotypeen.<BR><BR>Det r mnga delar som ska hra ihop och framfr allt tycker jag att den ska bertta en story om garen.\u00a0<BR><BR>Och om jag ska vara rlig, s anser jag att jag hittills har gjort ett par bra jobb. Det finns en gldje i att se ett fretag eller person f vxa fram i en enda bild.<BR><BR>Fr mig ska en logotype bertta ngot om personenfretaget. Den ska helst kunna st fr sig sjlv, som en ikon. Och allra helst ska den ha en liten twist. <BR><BR> En logotype som inte finns hr r till Ekoskoter, men du kan <h3>DANIJELA\u00a0RUNDQVIST</h3><p>Danijela r en god vn som behvde hjlp med sin blogg. Sjlvklart fick hon en blogg och en logotype. Jag menar - klart att en hockeyhjltinna ska ha en ordentlig hockeylogotype.<BR><BR>Logotypeen har sitt ursprung frn amerikanska hockeyvrlden med massiva bokstver. Den har ftt en svensk touch med gula stjrnor p bl \'ismoln\'. Q:et i Rundqvist har bytts ut mot en puck dr ismolnen och stjrnorna blivit Q:ets svans. Stjrnorna representerar Danijelas fruktade slagskott.\u00a0<p><BR><BR><h3>NYREGISTRERING.SE</h3><p>Ett fretag som tillhandahll en tjnst dr man mnadsvis kunde f tillgng till nyregistrerade fretag.\u00a0<BR><BR>Logotypeen utgr frn bokstverna n och r (ny\u00a0registering).\u00a0<BR><BR>Den \'studsande effekten\' frn bokstverna symboliserar att man behver gra ett nedslag hos Nyregistrering.se fr att kunna ta sig vidare till de intressanta fretagen.\u00a0<p><BR><BR><h3>FATUHIVA\u00a0FILM</h3><p>Fatuhivas logotype hade en fristende figur i sin gamla logotype som jag renodlade och ramade in. Ramen r innerdelen av en filmcell, frmst fr att det r klich\u00e9aktigt med matarhl p en filmlogotype.\u00a0<p><BR><BR><h3>CHRISTIN\u00a0PHILIPSSON</h3><p>Christin r fotograf som behvde en enkel logotype och hon behvde den fort. Heslt skulle den vara lite personlig. Inget r bttre fr kreativiteten n lite tidspress. Den mest personliga logotypeen man har r sin namnteckning. Varfr inte? Det fick bli Christins autograf.\u00a0<BR><BR>Fr att gra det lite mer intressant ritade jag om den i 3D, vilket ocks gjorde att den fick en mjukare rundhet.<p></span>',
