      'show':'1',
      'headline':'Gr\'t Projektplanerare',
      'textShort':'En egenutvecklad projektplanerare',
      'textLong': [
        {'paragraph': '<span class=\'CapUp\'>Vad gr man nr man snabbt behver en projektplanerare?<span> <span class="CapDown">Svaret r att man helt enkelt bygger en sjlv.</span>'},
        {'paragraph': 'Nr jag var originalare p Stadium Promotion upptckte vi att projekten ramlade in allt snabbare och vi fick allt mindre verblick om vem som var ansvarig fr vad och vem som vntade p tillbakaspel och var projekten hade fr tidsplan.'},
        {'paragraph': 'Att kpa in en projektplanerare r inte bara en kostnad, det r inte skert att alla kommer att anvnda den, plus att den kanske inte passar i Stadium Promotions stt att arbeta.'},
        {'paragraph': 'Mitt frslag blev att bygga en egen projektplanerare, som vi sjlva kunde anpassa till vrt arbetsstt och mjlighet att bygga ut efter behov. Jag hade vid tidpunkten fr byggandet nnu inte lrt mig PHP varp jag byggde den i Classic ASP, JavaScript och Access, under tre veckor (till och frn eftersom jag hade andra tasks att skta upptill).  Koderna kanske inte r de snyggaste och turerna kring att lgga in och ta bort poster r inte optimala, men med tanke p tiden s r jag njd med resultatet.'},
        {'paragraph': 'Gr\'t har en enkel struktur. Man lgger in sitt projekt, r det fler inblandade bockar man fr deras namn, deras tasks och nr man frvntar sig terspel frn dessa personer. Man kan f upp en projektlista p alla projekt man sjlv har starta, men ven de projekt som ngon annan har startat och man sjlv r delaktig i. Ja, that\'s it! Mer n s hann jag inte med. Dock hade jag vidare planer p mejlfunktioner och grafisk projektstatus, men som sagt, tiden fanns inte.'},
        {'paragraph': 'Trots det antika ASP och att koppla ihop det med Access, r jag nd njd med resultatet. De rdiga koderna syns inte utt, som tur r. Det finns en tanke om att bygga om den i PHP och MySQL, men just nu har tiden inte rckt till.'},
        {'paragraph': 'Ordet Gr\'t r stsjtskt och betyder helt enkelt \'Gr det\' ocheller \'Redan gjort\'. Stadium var moderbolaget till Stadium Promotion och de satt i Norrkping.'},
      ],
