      'id':'26',
      'show':'1',
      'headline':'Casefilmer',
      'textShort':'Casefilmer till Stadium Promotion',
      'textLong': [
        {'paragraph': '<span class=\'CapUp\'>\'Hur snabbt kan en person gra sju fretagsfilm?\'<span> <span class="CapDown">Svaret r tre och en halv mnad (och d jobbade jag tidvis med andra tasks ocks).</span>'},
        {'paragraph': 'Filmerna var upprinnelsen i den process som pgtt i nstan tre r p Stadium Promotion. Man ville omdefiniera det man lite slarvigt kallar fr \'profilprodukter\' och istllet sl sig in p en mer medveten del i marknadsfringsmixen och bli ett hllbart reklamalternativ. Vad r mer hllbart n en produkt, i bra kvalitet, som anvnds dagligen?'},
        {'paragraph': 'Tillsammans med tv andra medarbetare satte vi oss ner och spnade. En av iderna som jag kom med var att gra korta filmer som repeterade sig sjlva. Frdelen med detta var att man kunde gra en kort film, men som kunde frlngas i en evig loop. Som alltid nr det gller vision och slutresultat, blev det ngra jmkningar, bland annat att just att filmerna skulle vara eviga loopar. Men att repetera filmerna fem sex gnger r tillrckligt.'},
        {'paragraph': 'En annan sak var ocks att de skulle vara roliga. Humor r en genvg till gillandet och vi hade en grundtanke till alla filmerna. Nu var det bara att stta igng.'},
        {'paragraph': '\'Den som r ovetande om det omjliga, gr det mjliga\' r en tanke s hr eftert. Visst - det fanns manus och ider, men sjlva arbetsprocessen var mycket dynamisk, fr att inte sga ngot obefintligt. Och det var nog det som gjorde det mjligt.'},
        {'paragraph': 'Jag fick anvnda alla medel och program fr att ta mig igenom filmerna. De innehll 3D-animeringar, rotoscoping, chroma key och lager p lager av compositing. Jag lrde mig en massa om arbetsflden och tekniska utmaningar. Det var verkligen lrorikt att huvudstupa kasta sig ut fr att lsa uppgiften innan semestern brjade.'},
        {'paragraph': 'Det blev bttre n jag trodde och med tanke p den snva tidsramen r jag riktigt njd med resultatet. Det r klar - idag hade jag nog planerat och varit mer noggrann innan jag satte igng och testat lite, men samtidigt hade jag inte tid till det.'},
        {'paragraph': 'Det var roligt att f gra och det var verkligen fantastiskt att jag fick chansen att verkligen lra mig alla delar som man bara lser om eller ser bakom kulisserna.'},
      ],
