      'headline':'Baktorget',
      'textShort':'En 3D-modellerad mssa till Bak&Choklad-mssan 2016',
      'textLong': [
        {'paragraph': '<span class=\'CapUp\'>En av de mest beskta delarna av mssan Bak&Choklad<span> <span class="CapDown">var Baktorget - dr medlemmar till branschorganisationen Sveriges bagare & konditorer stod och bakade under mssan.</span>'},
        {'paragraph': 'Designavdelningen p Stockholmsmssan hade jobbat fram montern och en av tankarna var att en konditormssa med SBKs logotype skulle blir kronan p verket. En riktig konditormssa var bestlld och skulle fotas, men den dk inte upp och tiden var knapp. Att producera en fyra meter lng cirkelvepa tar tid.'},
        {'paragraph': 'Min lsning blev att istllet modellera upp en konditormssa i 3D. I underlaget frn Designavdelningen fanns ett lgupplst foto p en mssa som jag anvnde som referens. Jag ville komma s nra den bilden som mjligt.'},
        {'paragraph': 'Det tog knappt tre timmar att modellera upp mssan, plus tre timmar att stta rtt struktur och ljus. Eftersom bilden behvde vara stor var jag lite orolig fr renderinstiden. Som tur var hade jag lite hjlp av en renderingsfarm allt som allt (med slutfix i Photoshop och slutmontering till tryckfrdigt original), tog det ca 8 timmar.'},
        {'paragraph': 'Jag fick stor uppskattning fr slutresultatet. P Stockholmsmssans Designavdelning sa de att bilden gav en illusion av en riktig mssa.'},
      ],
